import React, { Component } from "react";
import { S, wait } from "./alabsi";
import { userData, data } from "./Data";
import ItemCard from "./ItemCard";
import { gsap } from "gsap";
import MealCard from "./MealCard";

export default class FavoritePage extends Component {
	componentDidMount() {
		if (this.props.doAnimation) {
			this.introPageTransition();
		} else {
			this.introPageFadeTransition();
		}

		if (this.props.page === 2 || window.location.hash.includes("#Meals")) {
			S("#favoritePageMealsTab").css("border-bottom", "solid 5px white");
			S("#favoritePageFoodsTab").css("border-bottom", "solid 0px white");
			gsap.to("#favoritePageBodyContainer", { duration: 0, scrollTo: "#favoritePageBodymeal" });
			if (this.props.page === 2) window.history.replaceState("", "", "/favorites#Meals");
		}
	}

	renderFoodData = _ => {
		const favoritedata = userData.favorite.food.map(e => (e = Object.assign({}, data.filter(el => el.id === e)[0])));
		return favoritedata.map(e => (
			<ItemCard
				key={e.id}
				name={e.name}
				kcal={e.serving ? ~~((e.kcal / 100) * e.serving[0][1]) : e.kcal}
				serving={e.serving ? e.serving[0][0] : "كل (100) غرام"}
				id={e.id}
				description={e.description}
			/>
		));
	};

	renderMealData = _ => {
		let ComponentArray = [];
		for (let i in userData.favorite.meals) {
			const mealdata = userData.favorite.meals[i].map(
				e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
			);
			const kcalSum = mealdata.map(e => (e.kcal * (e.value * e.input)) / 100).reduce((a, b) => a + b);
			ComponentArray.unshift(<MealCard key={Math.random() * 1000} name={i} kcal={kcalSum} ingredients={mealdata.length} />);
		}
		return ComponentArray;
	};

	switchTabs = e => {
		e.target.style.borderBottom = "solid 5px white";
		if (e.target.nextElementSibling && window.location.hash.includes("#Meals")) {
			S("#favoritePageMealsTab").css("border-bottom", "solid 0px white");
			gsap.to("#favoritePageBodyContainer", { duration: 0.3, scrollTo: "#favoritePageBodyFood" });
			if (window.location.hash.includes("Meals") && window.location.hash.length === 6) {
				window.history.replaceState("", "", "/favorites");
			} else {
				window.history.replaceState("", "", "/favorites#" + window.location.hash.split("#")[2]);
			}
		} else if (e.target.previousElementSibling && !window.location.hash.includes("#Meals")) {
			S("#favoritePageFoodsTab").css("border-bottom", "solid 0px white");
			gsap.to("#favoritePageBodyContainer", { duration: 0.3, scrollTo: "#favoritePageBodymeal" });
			if (window.location.hash.length === 0) {
				window.history.replaceState("", "", "/favorites#Meals");
			} else {
				window.history.replaceState("", "", "/favorites#Meals#" + window.location.hash.split("#")[1]);
			}
		}
	};

	introPageTransition = async _ => {
		S("#favoritePageBack").removeAttr("style");
		S("#favoritePage")
			.css("position", "fixed")
			.css("bottom", "-100vh")
			.css("left", "50%")
			.css("opacity", "0")
			.css("transform", "translate(-50%, 100%)");
		S("#favoritePageHeader").css("position", "relative");
		await wait(10);
		S("#favoritePage")
			.css("bottom", "100%")
			.css("opacity", "1");
		await wait(S("#favoritePage").getStyle("transition-duration", true) * 1000);
		S("#favoritePage").removeAttr("style");
		S("#favoritePageHeader").removeAttr("style");
	};

	introPageFadeTransition = async _ => {
		if (window.location.pathname === "/favorites" && window.location.hash.length === 0) S("#favoritePageBack").css("display", "none");
		S("#favoritePage")
			.css("opacity", 0)
			.css("transition", "opacity 350ms");
		await wait(20);
		S("#favoritePage").css("opacity", 1);
		await wait(350);
		S("#favoritePage").removeAttr("style");
	};

	backButtonHandle = _ => {
		// if (window.location.pathname.includes("CreateNewMeal")) {
		// 	updateRenderState([<SearchPage key={Math.random() * 100} />]);
		// 	window.history.replaceState("", "", "/CreateNewMeal/search");
		// } else if (window.location.hash.length === 0) {
		// 	updateRenderState([<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />]);
		// 	window.history.replaceState("", "", "/");
		// } else {
		// 	updateRenderState([<SearchPage key={Math.random() * 100} title={this.checkHash()} />]);
		// 	window.history.replaceState("", "", "/search#" + window.location.hash.split("#")[1]);
		// }
		window.history.back();
	};

	// checkHash = _ => {
	// 	let hash;
	// 	switch (window.location.hash.split("#")[1]) {
	// 		case "breakfast":
	// 			hash = "الفطور";
	// 			break;
	// 		case "lunch":
	// 			hash = "الغداء";
	// 			break;
	// 		case "dinner":
	// 			hash = "العشاء";
	// 			break;
	// 		case "snacks":
	// 			hash = "وجبة خفيفة";
	// 			break;
	// 		default:
	// 			break;
	// 	}
	// 	return hash;
	// };

	render() {
		return (
			<div id='favoritePage'>
				<header id='favoritePageHeader'>
					<div id='favoritePageHeaderContainer'>
						<p id='favoritePageTitle'>المفضلة</p>
						<svg id='favoritePageBack' onClick={this.backButtonHandle} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
							<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
						</svg>
					</div>
					<div id='favoritePageTabsContainer'>
						<p id='favoritePageFoodsTab' className='favoritePageTabs' onClick={this.switchTabs}>
							الطعام
						</p>
						<p
							id='favoritePageMealsTab'
							className='favoritePageTabs'
							onClick={this.switchTabs}
							style={{
								display:
									window.location.pathname.includes("CreateNewMeal") || window.location.pathname.includes("EditMeal")
										? "none"
										: "inline-block"
							}}
						>
							الوجبات
						</p>
					</div>
				</header>
				<div id='favoritePageBodyContainer'>
					<div id='favoritePageBodyFood'>
						<p className='favoritePageIsEmpty' style={{ display: this.renderFoodData().length === 0 ? "block" : "none" }}>
							{this.renderFoodData().length === 0 ? "لا يوجد طعام في المفضلة" : ""}
						</p>
						{this.renderFoodData()}
					</div>
					<div id='favoritePageBodymeal'>
						<p className='favoritePageIsEmpty' style={{ display: this.renderMealData().length === 0 ? "block" : "none" }}>
							{this.renderMealData().length === 0 ? "لا يوجد وجبات في المفضلة" : ""}
						</p>
						{this.renderMealData()}
					</div>
				</div>
			</div>
		);
	}
}
