import React, { Component } from "react";
import { ProgressBar, S, wait } from "./alabsi";
import { userData, data } from "./Data";
import ItemCard from "./ItemCard";
import { updateRenderState } from ".";
import SearchPage from "./SearchPage";
import NavicationBar from "./NavicationBar";
import FavoritePage from "./FavoritePage";

let tmpOldName;
export default class CreateMeal extends Component {
	componentDidMount() {
		if (this.props.doAnimation) {
			this.introPageTransition();
		} else {
			this.introPageFadeTransition();
		}
		if (this.props.oldName) tmpOldName = this.props.oldName;
		this.addProgressBars();
		S("#CreateMealNameInput input").node.value = userData.favorite.tmpMeal.mealName;
	}

	returnData = _ => {
		if (userData.favorite.tmpMeal.mealContent.length > 0) {
			return userData.favorite.tmpMeal.mealContent.map(
				e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
			);
		} else {
			return undefined;
		}
	};

	calcNutrition = n => {
		const dataCollection = this.returnData();
		if (dataCollection) {
			return Number(
				dataCollection
					.map(e => (e[n] * (e.value * e.input)) / 100)
					.reduce((a, b) => a + b)
					.toFixed(1)
			);
		} else {
			return 0;
		}
	};

	renderMealCards = _ => {
		const dataCollection = this.returnData();
		if (dataCollection) {
			return dataCollection.map(e => (
				<ItemCard
					key={e.id + Math.random() * 1000}
					name={e.name}
					kcal={~~((e.kcal / 100) * (e.value * e.input))}
					serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter(el => el[1] === e.value)[0][0]}
					id={e.id}
					description={e.description}
					value={e.value}
					input={e.input}
				/>
			));
		}
	};

	introPageTransition = async _ => {
		S("#CreateMealPage")
			.css("position", "fixed")
			.css("bottom", "-100vh")
			.css("left", "50%")
			.css("opacity", "0")
			.css("transform", "translate(-50%, 100%)");
		S("#CreateMealHeader").css("position", "relative");
		await wait(10);
		S("#CreateMealPage")
			.css("bottom", "100%")
			.css("opacity", "1");
		await wait(S("#CreateMealPage").getStyle("transition-duration", true) * 1000);
		S("#CreateMealPage").removeAttr("style");
		S("#CreateMealHeader").removeAttr("style");
	};

	introPageFadeTransition = async _ => {
		S("#CreateMealPage")
			.css("opacity", 0)
			.css("transition", "opacity 350ms");
		await wait(20);
		S("#CreateMealPage").css("opacity", 1);
		await wait(350);
		S("#CreateMealPage").removeAttr("style");
	};

	proteinPercent = _ => Math.min(Math.max(Math.round(((this.calcNutrition("protein") * 4) / this.calcNutrition("kcal")) * 100), 0), 100);
	fatPercent = _ => Math.min(Math.max(Math.round(((this.calcNutrition("fat") * 9) / this.calcNutrition("kcal")) * 100), 0), 100);
	carbPercent = _ => Math.min(Math.max(Math.round(((this.calcNutrition("carb") * 4) / this.calcNutrition("kcal")) * 100), 0), 100);
	CalcCarbPercent = _ =>
		this.carbPercent() > 15
			? Math.min(Math.max(Math.round(((this.calcNutrition("carb") * 4) / this.calcNutrition("kcal")) * 100), 0), 100)
			: this.carbPercent();

	addProgressBars = _ => {
		ProgressBar("#CreateMealNutritionInfoCrabsPercent", {
			input: this.CalcCarbPercent(),
			R: 40,
			width: 5,
			fillColor: "none",
			progressColor: "#2b2b2b;",
			textColor: "#2b2b2b",
			backgroundColor: "#F0F0F0",
			lineCap: "none"
		});
		ProgressBar("#CreateMealNutritionInfoProteinPercent", {
			input: this.proteinPercent(),
			R: 40,
			width: 5,
			fillColor: "none",
			progressColor: "#2b2b2b",
			textColor: "#2b2b2b",
			backgroundColor: "#F0F0F0",
			lineCap: "none"
		});
		ProgressBar("#CreateMealNutritionInfoFatPercent", {
			input: this.fatPercent(),
			R: 40,
			width: 5,
			fillColor: "none",
			progressColor: "#2b2b2b",
			textColor: "#2b2b2b",
			backgroundColor: "#F0F0F0",
			lineCap: "none"
		});
		const carbsTitle = document.createElement("p");
		const proteinTitle = document.createElement("p");
		const fatTitle = document.createElement("p");
		carbsTitle.classList.add("CreateMealPercentTitles");
		proteinTitle.classList.add("CreateMealPercentTitles");
		fatTitle.classList.add("CreateMealPercentTitles");
		carbsTitle.innerHTML = "الكربوهيدرات";
		proteinTitle.innerHTML = "البروتين";
		fatTitle.innerHTML = "الدهون";
		document.getElementById("CreateMealNutritionInfoCrabsPercent").append(carbsTitle);
		document.getElementById("CreateMealNutritionInfoProteinPercent").append(proteinTitle);
		document.getElementById("CreateMealNutritionInfoFatPercent").append(fatTitle);
	};

	backButtonHandle = _ => window.history.back();

	createMealButtonHandle = _ => {
		const [name, content] = [userData.favorite.tmpMeal.mealName.toLowerCase(), userData.favorite.tmpMeal.mealContent];
		let isNameExist = false;
		for (let i in userData.favorite.meals) if (i.toLowerCase() === name) isNameExist = true;
		if (name.length > 0 && content.length > 0 && !isNameExist) {
			userData.favorite.meals[name] = [...content];
			userData.favorite.tmpMeal.mealName = "";
			userData.favorite.tmpMeal.mealContent = [];
			setTimeout(() => {
				window.history.pushState("", "", "/favorites");
				updateRenderState([<FavoritePage key={Math.random() * 100} page={2} />, <NavicationBar key={Math.random() * 100} />]);
			}, 100);
			this.props.goBackOnce ? window.history.go(-1) : window.history.go(-2);
		} else if (isNameExist) {
			S("#CreateMealNameInput input").css("border", "solid 1px red");
			this.showToast("هذا الاسم موجود بالفعل");
			window.scrollTo({ top: 0 });
		} else if (name.length === 0) {
			S("#CreateMealNameInput input").css("border", "solid 1px red");
			this.showToast("ادخل اسم الوجبة");
			window.scrollTo({ top: 0 });
		} else if (content.length === 0) {
			S("#addFoodToMeal").css("border", "solid 1px red");
			this.showToast("قم بإضافة طعام إلى الوجبة");
		}
	};

	createMealEditModeButtonHandle = _ => {
		const [name, oldName, content] = [
			userData.favorite.tmpMeal.mealName.toLowerCase(),
			this.props.oldName ? this.props.oldName.toLowerCase() : tmpOldName.toLowerCase(),
			userData.favorite.tmpMeal.mealContent
		];
		let doNameExist = false;
		for (let i in userData.favorite.meals) {
			if (i.toLowerCase() === name && i.toLowerCase() !== oldName) {
				doNameExist = true;
			}
		}
		if (name === oldName && content.length > 0) {
			userData.favorite.meals[oldName] = [...content];
			window.history.go(-2);
		} else if (name !== oldName && content.length > 0 && !doNameExist) {
			delete Object.assign(userData.favorite.meals, { [name]: [...content] })[oldName];
			window.history.go(-2);
		} else if (doNameExist) {
			S("#CreateMealNameInput input").css("border", "solid 1px red");
			this.showToast("هذا الاسم موجود بالفعل");
			window.scrollTo({ top: 0 });
		} else if (name.length === 0) {
			S("#CreateMealNameInput input").css("border", "solid 1px red");
			this.showToast("ادخل اسم الوجبة");
			window.scrollTo({ top: 0 });
		} else if (content.length === 0) {
			S("#addFoodToMeal").css("border", "solid 1px red");
			this.showToast("قم بإضافة طعام إلى الوجبة");
		}
	};

	addFoodToMealHandle = _ => {
		updateRenderState(<SearchPage key={Math.random() * 100} doAnimation={true} />);
		if (this.props.editMode) {
			window.history.pushState("", "", "/EditMeal/search");
		} else {
			window.history.pushState("", "", "/CreateNewMeal/search");
		}
	};

	mealNameInputOnChangeHandle = e => {
		userData.favorite.tmpMeal.mealName = e.target.value;
	};

	showToast = async text => {
		S("#CreateMealToast")
			.text(text)
			.css("display", "block");
		await wait(20);
		S("#CreateMealToast")
			.css("opacity", 1)
			.css("bottom", "75px");
		await wait(2000);
		S("#CreateMealToast")
			.css("opacity", 0)
			.css("bottom", "0px");
		await wait(350);
		S("#CreateMealToast").removeAttr("style");
	};

	render() {
		return (
			<div id='CreateMealPage'>
				<p id='CreateMealToast'></p>
				<header id='CreateMealHeader'>
					<svg id='CreateMealBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
						<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
					</svg>
					<p id='CreateMealHeaderTitle'>{this.props.editMode ? "تعديل الوجبة" : "إنشاء وجبة جديدة"}</p>
				</header>
				<div id='CreateMealNameInput'>
					<p>ادخل اسم الوجبة:</p>
					<input
						type='text'
						placeholder='اسم الوجبة'
						onClick={e => {
							S("#CreateMealNameInput input").removeAttr("style");
						}}
						onChange={this.mealNameInputOnChangeHandle}
					/>
				</div>
				<p id='CreateMealDone' onClick={this.props.editMode ? this.createMealEditModeButtonHandle : this.createMealButtonHandle}>
					{this.props.editMode ? "حفظ التغيرات" : "إنـشـاء الـوجـبـة"}
				</p>
				<p id='addFoodToMeal' onClick={this.addFoodToMealHandle}>
					<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
						<path d='M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z' />
					</svg>
					إضافة طعام إلى الوجبة
				</p>
				<div id='CreateMealCards'>{this.renderMealCards()}</div>
				<div id='CreateMealNutritionInfoContainer'>
					<p id='CreateMealNutritionInfoTitle'>المعلومات الغذائية</p>
					<div id='CreateMealNutritionInfoPercentContainer'>
						<div id='CreateMealNutritionInfoCrabsPercent'></div>
						<div id='CreateMealNutritionInfoProteinPercent'></div>
						<div id='CreateMealNutritionInfoFatPercent'></div>
					</div>
					<div id='CreateMealNutritionInfoDetail'>
						<div id='CreateMealNutritionInfoTitles'>
							<p className='CreateMealNutritionInfoMain'>السعرات الحرارية</p>
							<p className='CreateMealNutritionInfoMain'>البروتين</p>
							<p className='CreateMealNutritionInfoMain'>الكربوهيدرات</p>
							<p className='CreateMealNutritionInfoSub'>الألياف</p>
							<p className='CreateMealNutritionInfoSub'>السكر</p>
							<p className='CreateMealNutritionInfoMain'>الدهون</p>
							<p className='CreateMealNutritionInfoSub'>الدهون المشبعة</p>
							<p className='CreateMealNutritionInfoSub'>الدهون الغير مشبعة</p>
							<p className='CreateMealNutritionInfoMain'>آخر</p>
							<p className='CreateMealNutritionInfoSub'>الكوليسترول</p>
							<p className='CreateMealNutritionInfoSub'>الصوديوم</p>
							<p className='CreateMealNutritionInfoSub'>البوتاسيوم</p>
						</div>
						<div id='CreateMealNutritionInfoValues'>
							<p className='CreateMealNutritionInfoMain'>{this.calcNutrition("kcal")}</p>
							<p className='CreateMealNutritionInfoMain'>{this.calcNutrition("protein")} غ</p>
							<p className='CreateMealNutritionInfoMain'>{this.calcNutrition("carb")} غ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("fiber")} غ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("sugar")} غ</p>
							<p className='CreateMealNutritionInfoMain'>{this.calcNutrition("fat")} غ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("saturatedFat")} غ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("unsaturatedFat")} غ</p>
							<p className='CreateMealNutritionInfoMain' style={{ visibility: "hidden" }}>
								......
							</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("cholesterol").toFixed(0)} ملغ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("sodium").toFixed(0)} ملغ</p>
							<p className='CreateMealNutritionInfoSub'>{this.calcNutrition("potassium").toFixed(0)} ملغ</p>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
